@media all and (max-width: 767px)
{
    .row-20,.row-30
    {
        margin-left: -15px;
        margin-right: -15px;
    }

    .row-20 > div,.row-30 > div
    {
        padding-left: 15px;
        padding-right: 15px;
    }
}

